import React, { FunctionComponent } from 'react'
import { MarkdownRemark } from '@/graphql-types'
import { Link } from 'gatsby'
import ReactMarkdown from 'react-markdown'
import classnames from 'classnames'

const Styles = require('./PostItem.module.styl')

type Props = {
  node: MarkdownRemark
  className?: string
}

const monthInEng = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']

export const PostItem: FunctionComponent<Props> = ({ node, className }) => {
  return (
    <div className={classnames(Styles['post'], className)}>
      <div className={Styles['date']}>
        <div className={Styles['up']}>
          <div className={Styles['month']}>{monthInEng[new Date(node.frontmatter.date).getMonth()]}</div>
          <div className={Styles['day']}>{new Date(node.frontmatter.date).getDay()}</div>
        </div>
        <div className={Styles['year']}>{new Date(node.frontmatter.date).getFullYear()}</div>
      </div>
      <div className={Styles['content']}>
        <div className={Styles['header']}>
          <Link className={Styles['title']} to={`/post-${node.frontmatter.date}`}>
            {node.frontmatter.title || node.fields.slug}
          </Link>
        </div>
        <div className={Styles['info']}>
          {node.frontmatter.tags.map(tag => (
            <React.Fragment key={tag}>
              <i className='iconfont'>&#xe63e;</i>
              <Link to='/tags' className={Styles['tag']}>
                {tag}
              </Link>
              <span className={Styles['line']}>·</span>
            </React.Fragment>
          ))}
          {node.timeToRead + 1} min read
          <span className={Styles['time']}>
            <span className={Styles['line']}>·</span>
            {new Date(node.frontmatter.date).getFullYear()}
            <span>年</span>
            {new Date(node.frontmatter.date).getMonth() + 1}
            <span>月</span>
            {new Date(node.frontmatter.date).getDay()}
            <span>日</span>
          </span>
        </div>
        <div className={Styles['brief']}>
          <ReactMarkdown source={node.frontmatter.description || node.excerpt} />
        </div>
        <Link to={`/post-${node.frontmatter.date}`} className={Styles['more']}>
          Read More
        </Link>
      </div>
    </div>
  )
}
