import { graphql, navigate } from 'gatsby'
import React, { FunctionComponent } from 'react'
import { MarkdownRemarkConnection, Site } from '../graphql-types'
import ReactPaginate from 'react-paginate'
import Layout from '../components/layout/layout'
import SEO from '../components/seo'
import { animated, useTransition } from 'react-spring'
import { PostItem } from '../components/post-item'

const Styles = require('../styles/templates/post-list.module.styl')

type Props = {
  data: {
    allMarkdownRemark: MarkdownRemarkConnection
    site: Site
  }
  pageContext: {
    page: number
    limit: number
    skip: number
  }
  location: Location
}

const BlogPostList: FunctionComponent<Props> = props => {
  const { data } = props
  const { title: siteTitle } = data.site.siteMetadata
  const { edges: posts, totalCount } = data.allMarkdownRemark
  const { page, limit: size } = props.pageContext

  const pageCount = Math.ceil(totalCount / size)

  const transitions = useTransition(posts, post => post.node.fields.slug, {
    from: { transform: 'translate3d(200px,0,0)', opacity: 0 },
    enter: { transform: 'translate3d(0,0px,0)', opacity: 1 },
    config: { duration: 150 },
    trail: 5,
  })

  const handlePageChangeClick = (data: { selected: number }) => {
    const page = data.selected + 1
    if (page === 1) {
      navigate('/posts')
      return
    }

    navigate(`/posts/page/${page}`)
  }

  return (
    <Layout location={props.location} title={siteTitle}>
      <SEO title='All posts' />
      {transitions.map(({ item: { node }, props, key }) => {
        return (
          <animated.div key={key} style={props}>
            <PostItem node={node} />
          </animated.div>
        )
      })}
      <ReactPaginate
        pageCount={pageCount}
        pageRangeDisplayed={size}
        previousLabel={'←'}
        initialPage={page}
        nextLabel={'→'}
        onPageChange={handlePageChangeClick}
        containerClassName={Styles['pagination']}
      />
    </Layout>
  )
}

export default BlogPostList

export const query = graphql`
  query PostListQuery($skip: Int, $limit: Int) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(limit: $limit, skip: $skip, sort: { order: DESC, fields: frontmatter___date }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            description
            tags
            title
          }
          fields {
            slug
          }
          fileAbsolutePath
          wordCount {
            words
          }
          timeToRead
        }
      }
    }
    dataYaml {
      createPageConfig {
        activeLayout
        posts {
          listPath
        }
      }
    }
  }
`
